import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

// Async thunk for activating account
export const handleActiveAccount = createAsyncThunk(
  "account/handleActiveAccount",
  async (data, thunkAPI) => {
    console.log(data)
    try {
    //   const req = await fetch("http://localhost:5000/api/v1/auth/account_id/:id/active_account/:token", {
    //     method: "POST",
    //     credentials: "include",
    //     headers: {
    //       "Content-Type": "application/json"
    //     },
    //     body: JSON.stringify(data),
    //   });

    //   const res = await req.json();

    //   if (!req.ok) {
    //     return thunkAPI.rejectWithValue(res); // Error from server
    //   }

    //   console.log(res);
      return res;
    } catch (error) {
      console.log(error);
      return thunkAPI.rejectWithValue({ message: "Network error" });
    }
  }
);

// Initial state
const initialState = {
  activeAccount: null,
  loading: false,
  error: null,
};

// Slice
const ActiveAccountSlice = createSlice({
  name: "activeAccount",
  initialState,
  extraReducers: (builder) => {
    builder
      .addCase(handleActiveAccount.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(handleActiveAccount.fulfilled, (state, action) => {
        state.loading = false;
        state.activeAccount = action.payload;
      })
      .addCase(handleActiveAccount.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload;
      });
  },
});

export default ActiveAccountSlice.reducer;
